export default function Me() {
  const { t } = useTranslation();
  const togglePaymentMethodModal = useToggler();
  const togglePasswordChangeModal = useToggler();
  const togglePhoneVerifyModal = useToggler();
  const toggleEmailVerifyModal = useToggler();
  const toggleIDVerifyModal = useToggler();

  const {
    session,
    services: { user, security },
  } = useServiceContextHook();

  const [userData, setUserData] = useState(null);
  const [dataError, setDataError] = useState(null);
  const [cachedData, setCachedData] = useState({});
  const [confirmations, setConfirmation] = useState({
    login: { email: true, sms: false },
    transaction: { email: true, sms: false },
  });
  useState("EMAIL");

  useEffect(async () => {
    if (session?.user)
      try {
        setDataError(null);
        if (!cachedData?.length) {
          let { data, error } = await fetchUserData();
          if (error) throw new Error(error);
          setUserData(data);
        }
      } catch (error) {
        console.error(error);
        setDataError(error);
      }
    return user.abort;
  }, [session]);

  /**
   *
   * @param {Object} filter : ;
   * @returns
   */
  async function fetchUserData(
    filter = {
      include: ["orders", "security"],
    }
  ) {
    return await user.find(filter);
  }

  /**
   *
   * @param {String} type - Confirmation type - transaction, login
   */
  async function onConfirmationChange({ type, key, value }) {
    try {
      // send a request to update confirmation
      const newState = {
        ...confirmations,
        [type]: { ...confirmations[type], [key]: value },
      };
      let changes = {
        ...userData?.security?.confirmations,
        [type]: Object.keys(newState[type])
          ?.filter((k) => newState[type][k])
          .map((t) => String(t)?.toUpperCase()),
      };
      let { data, error } = await security.updateByID(userData?.security?.id, {
        confirmations: changes,
      });
      if (error) throw new Error(error.message);
      if (data) {
        notifySuccess("Done");

        setConfirmation(newState);
      }
    } catch (err) {
      console.error(err);
      notifyError(err.message);
    }
  }

  return dataError ? (
    // TODO: Add an Error Feedback here...
    <>Data Error</>
  ) : session?.user && userData ? (
    <>
      <div className="content">
        <section id="profile">
          <div className="container">
            <div
              className="row wow animate__animated fadeInDown"
              data-wow-delay="0.2s"
            >
              <div className="col-12">
                <h4>{t("My profile")}</h4>
                <div className="user_info clear">
                  <div className="nickname">
                    <span className="text-uppercase">
                      {userData?.profile?.pname && userData?.profile?.pname[0]}
                    </span>
                    <strong className="text-uppercase">
                      {userData?.profile?.pname}
                    </strong>
                    {userData?.verified && (
                      <i className="fas fa-check-circle"></i>
                    )}
                    <a href="#" className="btn_edit">
                      <i className="fas fa-edit"></i>
                      {t("Edit")}
                    </a>
                  </div>
                  <div className="user_id">
                    {t("User ID:")} <span>{userData?.id}</span>
                  </div>
                </div>
                <br />
                <p>
                  <strong className="d-block">{t("Last login time :")}</strong>{" "}
                  <span>{userData?.login_at}</span>
                </p>
                <p>
                  <strong className="d-block">IP :</strong>
                  <span>
                    {Array.isArray(session?.user?.security?.ip_address) &&
                      session?.user?.security?.ip_address.join(", ")}
                  </span>
                </p>

                <div>
                  <a
                    onClick={user?.logout}
                    href="#"
                    aria-label="logout"
                    role="button"
                  >
                    {t("Logout")}
                  </a>
                </div>
                <div className="bottom clear">
                  <dl>
                    <dt>Total orders</dt>
                    <dd>{userData?.total_orders}</dd>
                  </dl>
                  <dl>
                    <dt>Completion rate</dt>
                    <dd>
                      {userData?.total_orders &&
                        (userData?.total_completed_orders /
                          userData?.total_orders) *
                          100}
                      %
                    </dd>
                  </dl>
                  <dl>
                    <dt>Trade Review(Positive/Negative)</dt>
                    <dd>
                      <span className="positive">
                        {userData?.total_positive_reviews}
                      </span>
                      /
                      <span className="negative">
                        {userData?.total_negative_reviews}
                      </span>
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="settings" title="Notification settings">
          <div className="container">
            <div
              className="row wow animate__animated fadeInDown"
              data-wow-delay="0.4s"
            >
              <div className="col-12">
                <h4>{t("Notification Settings")}</h4>
                <div className="table_container">
                  <table>
                    <thead>
                      <tr>
                        <th></th>
                        <th>{t("Email")}</th>
                        <th>{t("SMS")}</th>
                      </tr>
                    </thead>
                    <tbody>
                      {confirmations &&
                        Object.entries(confirmations).map(([type, tvalue]) => {
                          return (
                            <tr key={type}>
                              <td className="text-capitalize">
                                {t(String(type)?.replace(/_/gi, " "))}
                              </td>

                              {Object.entries(tvalue).map(([key, value]) => (
                                <td key={`${type}-${key}`}>
                                  <Form.Check
                                    type="switch"
                                    id={`switch-${type}-${key}`}
                                    name={`${type}-${key}`}
                                    className="custom-switch-md text-center"
                                    onChange={() => {
                                      onConfirmationChange({
                                        type,
                                        key,
                                        value: !value,
                                      });
                                    }}
                                    defaultValue={value}
                                    defaultChecked={userData?.security?.confirmations[
                                      type
                                    ].includes(String(key)?.toUpperCase())}
                                  />
                                </td>
                              ))}
                            </tr>
                          );
                        })}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section id="securityTop">
          <div className="container">
            <div
              className="row wow animate__animated fadeInDown"
              data-wow-delay="0.6s"
            >
              <div className="col-12">
                <h4>{t("Security")}</h4>
                <ul>
                  <li className="password clear">
                    <ChangePasswordSetting />
                  </li>
                  <li className="recommended clear">
                    <GoogleAuthSetting />
                  </li>
                  <li className="phone clear">
                    <PhoneVerificationSetting />
                  </li>
                  <li className="email clear">
                    <MailVerificationSetting />
                  </li>
                  <li className="verification clear">
                    <img src={icon_verification} alt="Identity Verification" />
                    <dl>
                      <dt>{t("Identity Verification")}</dt>
                      <dd>
                        {t(
                          "Identity Verification can protect your account security and increase transaction limits."
                        )}
                      </dd>
                    </dl>
                    <a href="#" className="btn btn_enable">
                      {t("Enable")}
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </section>

        <PaymentMethodSetting userData={userData?.profile?.payment_methods} />
      </div>
    </>
  ) : (
    <div style={{ minHeight: "50vh" }}>Must login</div>
  );
}

