function PaymentMethodSetting({ userData }) {
  const {
    services: { profile },
    session,
  } = useServiceContextHook();
  const { t } = useTranslation();
  const togglePaymentMethodModal = useToggler();
  const [paymentData, setPaymentData] = useState(userData);

  /**
   * @description sends server request to update user data
   * @param {Object} changes
   * @returns
   */
  async function updatePaymentMethod(changes) {
    // Get current user profile ID
    let profile_id = session?.user?.profile?.profile_id;

    if (profile_id)
      return await profile.updateByID(profile_id, {
        payment_methods: changes,
      });
    return { error: new Error("profile ID not found!") };
  }

  /**
   * @description Handles payment method removal event
   * @param {String} key
   */
  async function onRemovePaymentMethod(key) {
    try {
      // Make a clone of the original payment data object
      let clone = Object.assign({}, paymentData);
      delete clone[key]; //Delete the key and its values

      // Make a network request
      let { data: _data, error, message } = await updatePaymentMethod(clone);
      // Handle network request response
      if (!_data) {
        throw new Error(
          error?.message || message || "Server error! Operation not completed"
        );
      }
      // if success
      setPaymentData(clone);
      notifySuccess("Done");
    } catch (err) {
      console.error(err);
      notifyError(err.message);
    }
  }

  /**
   * @description handles the update event
   * @param {Object} changes
   */
  async function onUpdate(changes) {
    try {
      // Make network update request
      let {
        data: _data,
        error,
        message,
      } = await updatePaymentMethod({ ...paymentData, ...changes });
      // handle network request response
      if (!_data) {
        throw new Error(
          error?.message || message || "Server error! Operation not completed"
        );
      }
      // on success
      setPaymentData({ ...paymentData, ...changes });
      notifySuccess("Done");
    } catch (err) {
      console.error(err);
      notifyError(err.message);
    }
  }

  return (
    <section id="securityBottom">
      {/* Modal */}
      {togglePaymentMethodModal.isOpen ? (
        <PaymentMethodModal
          {...{ setData: setPaymentData }}
          isOpen={togglePaymentMethodModal.isOpen}
          onClose={togglePaymentMethodModal.onClose}
          payload={togglePaymentMethodModal?.toggledPayload}
        ></PaymentMethodModal>
      ) : null}
      <div className="container">
        <div
          className="row wow animate__animated fadeInDown"
          data-wow-delay="0.8s"
        >
          <div className="" style={{ padding: 30 }}>
            <div
              className="d-flex justify-content-between"
              style={{ flexWrap: "wrap" }}
            >
              <h2 className="h3" style={{ minWidth: 300 }}>
                {t("payment_methods")}
              </h2>
              <div
                className="d-flex justify-content-end"
                style={{ flex: "1" }}
              ></div>
            </div>

            <div style={{ display: "flex", flexDirection: "column", gap: 10 }}>
              {" "}
              <div className="p2p">
                <span>{t("P2P")}</span>
              </div>
              <div>
                {t(
                  "P2P payment methods: When you sell cryptocurrencies, the payment method added will be displayed to buyer as options to accept payment, please ensure that the account owner’s name is consistent with your verified name on Binance. You can add up to 20 pay ment methods"
                )}
              </div>
              <div>
                <Button
                  className="btn btn-primary"
                  onClick={() =>
                    togglePaymentMethodModal.onOpen({
                      data: paymentData,
                      onUpdate,
                    })
                  }
                >
                  + Add
                </Button>
              </div>
              {paymentData ? (
                <>
                  {/* LISTING */}
                  {Object.entries(paymentData).map(([key, value]) => (
                    <div className={`wechat key`} key={key}>
                      <div className="top clear">
                        <h5 className="text-capitalize h5">
                          {t(String(key).replace(/_/gi, " "))}
                        </h5>
                        <ul className="clear">
                          <li>
                            <button
                              type="button"
                              onClick={() =>
                                togglePaymentMethodModal.onOpen({
                                  data: paymentData,
                                  type: key,
                                  onUpdate,
                                })
                              }
                            >
                              {t("Edit")}
                            </button>
                          </li>
                          <li>
                            <button
                              type="button"
                              onClick={() => onRemovePaymentMethod(key)}
                            >
                              {t("Delete")}
                            </button>
                          </li>
                        </ul>
                      </div>
                      <div className="bottom clear">
                        {Object.entries(value).map(([k, v]) => {
                          return (
                            <dl key={k}>
                              <dt className="text-capitalize">
                                {String(k).replace(/_/gi, " ")}
                              </dt>
                              <dd className="text-capitalize">
                                {String(v).replace(/_/gi, " ")}
                              </dd>
                            </dl>
                          );
                        })}
                      </div>
                    </div>
                  ))}
                </>
              ) : (
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                    alignItems: "center",
                    minHeight: 200,
                  }}
                >
                  You have not added any payment method yet
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

function PaymentMethodModal({
  isOpen,
  onClose,
  payload = { type: null, data: {}, onUpdate: () => null },
}) {
  const [method, setMethod] = useState(null);

  useEffect(() => {
    setMethod(payload?.type || "bank_transfer");
  }, [payload]);

  function onChangeMethod({ target }) {
    setMethod(target?.value);
  }

  return method ? (
    <>
      <Modal show={isOpen} onHide={onClose}>
        <Modal.Header>Payment method</Modal.Header>
        <Modal.Body style={{ padding: 0, paddingBottom: 20 }}>
          <FormControl
            component="fieldset"
            style={{ paddingLeft: 20, paddingTop: 10 }}
          >
            <RadioGroup
              row
              aria-label="payment_method"
              name="payment_method"
              value={method}
              onChange={onChangeMethod}
            >
              <FormControlLabel
                value="bank_transfer"
                control={<Radio />}
                label="Bank transfer"
              />
              <FormControlLabel
                value="wechat"
                control={<Radio />}
                label="Wechat"
              />

              <FormControlLabel
                value="alipay"
                control={<Radio />}
                label="Alipay"
              />
            </RadioGroup>
          </FormControl>
          <hr />
          <div>
            {method == "bank_transfer" ? (
              <BankTransferForm {...payload} />
            ) : null}
            {method == "wechat" ? <WechatForm {...payload} /> : null}
            {method == "alipay" ? <AlipayForm {...payload} /> : null}
          </div>
        </Modal.Body>
      </Modal>
    </>
  ) : (
    "...loading"
  );
}

function BankTransferForm({ data = {}, onUpdate = () => null }) {
  const { bank_transfer = {} } = data || {};

  const initialValues = {
    bank_name: bank_transfer?.bank_name || "",
    bank_code: bank_transfer?.bank_code || "",
    account_name: bank_transfer?.account_name || "",
    account_number: bank_transfer?.account_number || "",
    currency: bank_transfer?.currency || "USD",
  };

  return (
    <Formik
      {...{ initialValues }}
      // validate={(values) => {}}
      onSubmit={async (values, { setSubmitting }) => {
        try {
          onUpdate({ bank_transfer: values });
        } catch (error) {
          console.error(error);
        } finally {
          setSubmitting(false);
        }
      }}
    >
      {({
        values,
        errors,
        isSubmitting,
        handleSubmit,
        handleChange,
        setFieldValue,
        handleBlur,
        touched,
      }) => (
        <Form
          onSubmit={handleSubmit}
          style={{
            display: "flex",
            flexWrap: "wrap",
            flexDirection: "column",
            padding: "10px 20px",
            gap: 20,
            justifyContent: "center",
          }}
        >
          <TextField
            size="small"
            variant="outlined"
            id="bank_name"
            label="Bank name"
            onChange={handleChange}
            defaultValue={values?.bank_name}
          />

          {/* Account number */}
          <TextField
            label="Bank Code"
            id="bank_code"
            size="small"
            variant="outlined"
            onChange={handleChange}
            defaultValue={values?.bank_code}
            inputProps={{
              pattern: "^[A-Za-z0-9]{8,11}$",
              minLength: 8,
              maxLength: 11,
            }}
          />

          {/* Account name */}
          <TextField
            id="account_name"
            label="Account name"
            variant="outlined"
            size="small"
            onChange={handleChange}
            defaultValue={values?.account_name}
          />

          {/* Account number */}
          <TextField
            size="small"
            variant="outlined"
            id="account_number"
            label="Account number / IBAN"
            onChange={handleChange}
            defaultValue={values?.account_number}
            inputProps={{ pattern: "^[A-Za-z0-9-]{1,17}$", maxLength: 15 }}
          />
          <FiatCurrencySelector
            all={false}
            attributes={{
              value: values?.currency,
              style: {
                padding: 10,
                backgroundColor: "white",
                boxShadow: "rgba(78, 77, 77, 50%) 0px 0px 0px 1px",
                borderRadius: 4,
              },
            }}
            onChange={(val) => setFieldValue("currency", val)}
          />
          <Button disabled={isSubmitting} type="submit">
            {isSubmitting ? "Saving" : "Save"}
          </Button>
        </Form>
      )}
    </Formik>
  );
}

