import React, { useEffect, useState, useRef } from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";

import logo from "../../app-assets/images/logo/logo.png";
import { useSelector } from "react-redux";

import icon_user from "../../app-assets/images/icon/icon_user.png";
import _actions from "../../../../_actions";

// Multi language
import { useTranslation } from "react-i18next";

export const Header = () => {
  const { t } = useTranslation();

  const session = useSelector((state) => state?.session);
  const [scroll, setScroll] = useState(false);
  const cur_loc = window.location.pathname;

  const [menuToggle, setMenuToggle] = useState(false);
  const handleClick = () => {
    setMenuToggle(!menuToggle);
  };

  useEffect(() => {
    const scrollFn = () => {
      setScroll(window.scrollY > 100);
    };
    window.addEventListener("scroll", scrollFn);

    return () => {
      if (window?.removeEventListener) {
        // For all major browsers, except IE 8 and earlier
        window?.removeEventListener("scroll", scrollFn);
      } else if (window?.detachEvent) {
        // For IE 8 and earlier versions
        window?.detachEvent("scroll", scrollFn);
      }
    };
  }, []);

  return (
    <header
      className="header1"
      style={
        scroll
          ? { backgroundColor: "rgba(0,0,0,0.75)" }
          : { backgroundColor: "transparent" }
      }
    >
      <div className="inner clear">
        <h1>
          <a href="/">
            <img src={logo} alt-="CoinTC" />
          </a>
        </h1>
        <nav>
          <h2 className="hidden">메인메뉴</h2>
          <div className="gnb_pc clear">
            <ul className="clear">
              <li
                className={
                  cur_loc === "/trades" ||
                  cur_loc === "/ad_create" ||
                  cur_loc === "/ad_payment_method" ||
                  cur_loc === "/ad_contract"
                    ? "on"
                    : ""
                }
              >
                <Link to="/trades">{t("P2P Trade")}</Link>
              </li>
              <li className={cur_loc === "/order" ? "on" : ""}>
                <Link to="/order">{t("Orders")}</Link>
              </li>
              <li className={cur_loc === "/wallet" ? "on" : ""}>
                <Link to="/wallet">{t("Wallet")}</Link>
              </li>
              <li className={cur_loc === "/affiliate" ? "on" : ""}>
                <Link to="/affiliate">{t("Affiliate")}</Link>
              </li>
              <li className={cur_loc === "/support" ? "on" : ""}>
                <Link to="/support">{t("Support")}</Link>
              </li>
            </ul>
          </div>
          <div className="search_pc">
            <div className="form-group has-search mb-0">
              <span className="fa fa-search form-control-feedback"></span>
              <input
                type="text"
                className="form-control"
                placeholder={t("Search blocks,transactions,hash...")}
              />
            </div>
          </div>

          {session?.user ? (
            <>
              <div className="user clear">
                <a href="/my-page">
                  <img src={icon_user} alt="My page" />
                  <p>{t("My page")}</p>
                </a>
              </div>
            </>
          ) : (
            <div className="user_btn clear">
              <a href="/login" className="btn btn_login text-white mr-3">
                {t("Login")}
              </a>
              <a href="/signup" className="btn btn_signup">
                {t("Sign Up")}
              </a>
            </div>
          )}

          <div
            className="side_menu"
            style={menuToggle ? { right: "0px" } : { right: "-250px" }}
          >
            <div className="burger_box">
              <div className="menu-icon-container">
                <a
                  href="#"
                  className={
                    "menu-icon js-menu_toggle " +
                    (menuToggle ? "opened" : "closed")
                  }
                  onClick={handleClick}
                >
                  <span className="menu-icon_box">
                    <span className="menu-icon_line menu-icon_line--1"></span>
                    <span className="menu-icon_line menu-icon_line--2"></span>
                    <span className="menu-icon_line menu-icon_line--3"></span>
                  </span>
                </a>
              </div>
            </div>
            <div className="search_m">
              <div className="form-group has-search">
                <span className="fa fa-search form-control-feedback"></span>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Search blocks,transactions,hash..."
                />
              </div>
            </div>
            <div className="gnb_m">
              <ul className="clear">
                <li>
                  <a href="/trades">P2P Trade</a>
                </li>
                <li>
                  <a href="/order">Orders</a>
                </li>
                <li>
                  <a href="/wallet">Wallet</a>
                </li>
                <li>
                  <a href="/affiliate">Affiliate</a>
                </li>
                <li>
                  <a href="/support">Support</a>
                </li>
              </ul>
            </div>
            {session?.user ? (
              <>My page</>
            ) : (
              <div className="user_m">
                <a href="/login" className="btn btn_login text-white mb-2">
                  Login
                </a>
                <a href="/signup" className="btn btn_signup">
                  Sign Up
                </a>
              </div>
            )}
          </div>
        </nav>
      </div>
    </header>
  );
};
